.card-title.h5 {
    position: relative;
}
.card-title.h5:after {
    content: '';
    background: #2572ff;
    width: 20%;
    height: 5px;
    position: absolute;
    left: 0;
    bottom: -9px;
}
p.card-text {
    padding: 10px 0px;
}
span.title {
    text-align: center;
    display: inline-block;
    padding: 50px;
    font-size: 4em;
    font-family: serif;
    font-weight: bold;
    position: relative;
}
span.title::after {
    content: '';
    background-color: #2572ff;
    width: 100%;
    height: 5px;
    position: absolute;
    left: 0;
    bottom: 43px;
}
